import { RANGE_VALUE_RE } from "../consts";
/**
 *
 * @param value
 */

export function normRangeValues(value) {
  var offset = String(value).match(RANGE_VALUE_RE);

  if (offset) {
    var modifier = offset[5] ? 'p' : '';
    return "".concat(offset[1] || offset[4]).concat(modifier);
  } else {
    return value;
  }
}